body {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

@keyframes rotate-hand {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__pin {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

.stopwatch__pin:before {
  content: "";
  transform-origin: bottom;
  z-index: -1;
  background-color: #0700ff;
  border-radius: 0;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__pin:after {
  content: "";
  transform-origin: bottom;
  z-index: -1;
  background-color: #2c8000;
  border-radius: 0;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hand;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch--speed-up .stopwatch__pin:before {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__pin:after {
  animation-duration: 10s;
}
